@import "ScssHelpers/helpers-all.scss";

.slider_controls {
	@include flex_block(row, nowrap, flex-start, center, center);
}

.slider_controls__pagination {
	color: $gold_2;
	letter-spacing: .11em;
	text-transform: uppercase;

	@include bp($point_2, min) {
		font-size: 2rem;
		font-weight: 300;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
		font-weight: 400;

		.banner_slider & {
			letter-spacing: -.02em;
		}
	}
}
