@import "ScssHelpers/helpers-all.scss";

.post_preview_card {

	@include bp($point_2, min) {
		width: 50%;
		padding: 0 2.3rem 5rem 0;
	}

	@include bp($point_3) {
		width: 100%;
		padding: 0 2.3rem 1.8rem 0;
	}
}

.post_preview_card__img_link {
	display: block;

	&:hover,
	&:focus {
		.post_preview_card__img_wrap::before {
			opacity: 0.6;
		}
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.5rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.8rem;
		}
	}
}

.post_preview_card__img_wrap {
	position: relative;

	&::before {
		z-index: 2;
		pointer-events: none;

		width: 100%;
		height: 100%;

		background-image: linear-gradient(5deg, $black 0%, rgba($black, 0) 100%);
		opacity: 0;
		content: "";

		@include stretch;
		@include transition_opacity;
	}
}

.post_preview_card__img_w {
	position: relative;
	display: block;
	width: 100%;
	height: 100%;

	@include bp($point_2, min) {
		max-height: 28rem;
		padding-bottom: 46.6%;
	}

	@include bp($point_3) {
		padding-bottom: 57%;
	}
}

.post_preview_card__img {
	@include stretch;
	@include cover_img;
}

.post_preview_card__title {
	color: $black;

	@include h3;

	@include bp($point_2, min) {
		letter-spacing: -0.018em;
	}

	@include bp($point_3) {
		letter-spacing: -0.03em;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1rem;
		}

		@include bp($point_3) {
			margin-bottom: .7rem;
		}
	}
}

.post_preview_card__text {
	@include text;

	&:not(:last-child) {
		margin-bottom: 1.7rem;
	}

	@include bp($point_2, min) {
		width: 90%;
		line-height: 1.48;
	}

	@include bp($point_3) {
		letter-spacing: -.015em;
		line-height: 1.41;
	}
}

.post_preview_card__btn {
	@include bp($point_3) {
		display: none;
	}
}
