@import "ScssHelpers/helpers-all.scss";

.post_preview {
	// padding: 7.9rem var(--gl-indent) 8.8rem!important;

	background-color: $beige_1;

	&.post_preview--bg_mod_1 {
		background-color: $gray_3;
	}

	&.post_preview--bg_mod_2 {
		background-color: $white;
	}

	@include bp($point_2, min) {
		padding-top: 8rem;
		padding-bottom: 9rem;
	}
}

.post_preview__wrap {
	@include flex_block(row, wrap, flex-start);

	margin: 0 -2.3rem -5rem 0;
}

.post_preview__video_trigger {
	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 5;

	width: 10.4rem;
	height: 10.4rem;
	
	color: $white;

	transform: translate(-50%, -50%);

	&:hover {
		transform: translate(-50%, -50%) scale(1.1);
		color: $gold_2;
	}

	@include transition_all;
}
