@import "ScssHelpers/helpers-all.scss";

.learn_home_about {
	@include bp($point_2, min) {
		padding-top: 9.4rem;
		padding-bottom: 9.4rem;
	}
	// padding-top: 6.4rem;
	// padding-bottom: 9.4rem;
}

.learn_home_about__title {
	@include h3;

	@include bp($point_4, min) {
		letter-spacing: -0.016em;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	@include bp($point_4 - 1) {
		letter-spacing: -0.028em;

		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}
	}
}

.learn_home_about__list {
	@include bp($point_4, min) {
		@include flex_block(row, wrap, flex-start, stretch);

		margin: 0 -2.4rem -2.4rem 0;
	}
}

.learn_home_about__list_item {
	@include bp($point_4, min) {
		width: 50%;
		padding: 0 2.4rem 2.4rem 0;
	}

	@include bp($point_4 - 1) {
		width: 100%;

		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}
}

.learn_home_about_item {
	@include flex_block(column, nowrap, flex-end, flex-start);

	position: relative;
	width: 100%;
	height: 100%;

	&::before,&::after{
		z-index: 1;
		
		width: 100%;
		height: 100%;

		pointer-events: none;
		content:'';
		
		@include stretch;
		@include transition_opacity;
	}
	&::before {
		opacity: 0.6;
		background: linear-gradient(5deg, $black 0%, rgba($black, 0) 100%);
	}
	&::after{
		opacity: 0;
		background: $gradient_2;
	}

	&:hover,
	&:focus {
		text-decoration: none;

		&::before {
			opacity: 0;
		}
		&::after{
			opacity: .8;
		}
	}

	@include bp($point_4, min) {
		min-height: 37.2rem;
		padding: 4.3rem 4.4rem;
	}

	@include bp($point_4 - 1) {
		min-height: 15.2rem;
		padding: 2rem;
	}
}

.learn_home_about_item__img {
	z-index: 0;

	@include stretch;
}

.learn_home_about__img {
	@include cover_img;
}

.learn_home_about_item__content {
	position: relative;
	z-index: 2;
}

.learn_home_about_item__title {
	font-weight: 500;

	color: $white;

	@include bp($point_4, min) {
		font-size: 4.2rem;
		letter-spacing: -0.016em;
	}

	@include bp($point_4 - 1) {
		font-size: 2.2rem;
		letter-spacing: -0.03em;
	}
}

.learn_home_about_item__descr {
	@include text;

	color: $white;

	@include bp($point_4, min) {
		line-height: 1.4;
	}

	@include bp($point_4 - 1) {
		line-height: 1.14;
		letter-spacing: -0.018em;
	}
}
